<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Alta de Usuarios
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarUsuario()">
          <v-text-field
            v-model="users.username"
            label="Usuario"
            :counter="10"
            outlined
            prepend-icon="mdi-account-tie"
            required
          />
          <v-text-field
            v-model="users.email"
            label="E-mail"
            outlined
            prepend-icon="mdi-email"
            required
          />

          <v-text-field
            v-model="users.password"
            label="Contraseña"
            outlined
            prepend-icon="mdi-lock"
            type="password"
            required
          />

          <v-select
            v-model="roleSeleccionado"
            label="Rol"
            :items="rol"
            name="roleSeleccionado"
            item-text="name"
            outlined
            prepend-icon="mdi-lock"
            required
          />

          <v-checkbox
            label="¿Estás de acuerdo?"
            required
          />

          <v-card-actions>
            <v-btn
              to="/usuarios"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'

  const urlGetRoles = 'https://sistema-adv.herokuapp.com/api/roles'
  const urlPostUsers = 'https://sistema-adv.herokuapp.com/api/users'

  export default {
    name: 'CrearUsuario',
    data () {
      return {
        roleSeleccionado: {},
        rol: [],
        users: {
          username: '',
          email: '',
          password: '',
          roles: '',
        },
      }
    },
    mounted () {
      this.ObtenerRoles()
    },
    methods: {
      ObtenerRoles () {
        axios
          .get(urlGetRoles)
          .then((response) => {
            this.rol = response.data
          })
          .catch((e) => {
            console.log(e)
          })
      },
      guardarUsuario () {
        // Capturo el v-select
        this.users.roles = this.roleSeleccionado
        // console.log(this.users);
        const router = this.$router
        const params = this.users

        axios.post(urlPostUsers, params)
          .then(res => {
            // console.log(this.roleSeleccionado);
            // console.log(params);
            router.push('/usuarios')
            this.limpiarCampos()
          })
          .catch(e => {
            console.log(e)
          })
      },
      limpiarCampos () {
        this.users = ''
      },
    },
  }
</script>
